.icon {
  position: relative;

  display: block;
  width: 100%;
  padding-bottom: 100%;
  
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  text-decoration: none;
  
  &__tooltip {
    position: absolute;
    right: 100%;
    top: 50%;
    transform: translateY(-50%);
      
    display: block;
    width: max-content;
    padding-right: 12px;

    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    color: $color-black;
    letter-spacing: 2px;
    text-transform: uppercase;
    opacity: 0;
    pointer-events: none;
    transition: opacity $transion;

  }
   

  &:hover &__tooltip {
    opacity: 1;
    pointer-events: all;
  }

  &--phone {
    background-image: url(../images/tel.svg);
  }

  &--menu {
    background-image: url(../images/menu.svg);
    
    &:hover {
      background-image: url(../images/menu-hover.svg);
    }

  }
  
  &--cross {
    background-image: url(../images/cross.svg);
  }
  
}
